import request from '../../utils/request.js'
export default {
	data() {
		return {
			phone:'13933311138',
			password:'lijianhui',
			
		}
	},
	methods: {
		
		//登录
		async login(){
			let {phone,password} = this
			//判断是否输入手机号和密码
			if(!phone || !password) {
				uni.showToast({
				    title: '手机号和密码不能为空',
				    duration: 2000,
						icon:'none'
				});
				return
			}
			//判断手机号格式是否正确
			let phoneReg = /^1(3|4|5|6|7|8|9)\d{9}$/;
			if(!phoneReg.test(phone)){
				uni.showToast({
				    title: '手机号输入不合法',
				    duration: 2000,
						icon:'none'
				});
				return
			}
			// let result = await request('/login/cellphone', {phone, password, isLogin: true});
			try{
				await this.$store.dispatch('getUserInfo',{phone,password,isLogin:true})
				uni.showToast({
				    title: '登录成功',
				    duration: 2000,
						icon:'success',
				})
				uni.redirectTo({
					url:"/pages/personal/personal"
				})
			}catch(e){
				uni.showToast({
				    title: `${e}`,
				    duration: 2000,
						icon:'none',
				})
			}
		}
	}
}
